
// expert
.expert {
    &__title {
        margin-bottom: 40px;
    }

    &__wrap {
        display: flex;
        border-radius: 20px;
        background: #fbfbfb;
    }

    &__picture {
        display: block;
        width: 100%;
        max-width: 50%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 20px;
        }
    }

    &__contant {
        padding: 60px;
        width: 100%;
        max-width: 50%;
    }

    &__text {
        font-size: 20px;
        line-height: 1.5;
        color: #212121;
        margin-bottom: 40px;
        & a {
            color: #212121;
            text-decoration: underline;
        }
        & p:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    &__doc {
        border-radius: 20px;
        padding: 24px;
        background: #997000;
        color: #fff;

        &-fio {
            font-weight: 600;
            font-size: 20px;
            line-height: 1.1;
            margin-bottom: 16px;
        }

        &-post {
            font-size: 16px;
            line-height: 1.4;
        }
    }
}

@media (max-width: 1180px) {
    .expert {
        &__wrap {
            flex-direction: column;
        }
    
        &__picture {
            max-width: 100%;
            & img {
                max-height: 500px;
                object-position: top;
            }
        }
    
        &__contant {
            max-width: 100%;
        }
    }
}
@media (max-width: 768px) {
    .expert {
        &__title {
            margin-bottom: 20px;
        }
        &__picture {
            & img {
                min-height: 320px;
                max-height: 380px;
            }
        }
    
        &__contant {
            padding: 40px 10px 20px;
        }
    
        &__text {
            font-size: 16px;
            margin-bottom: 20px;
        }

        &__doc {
            padding: 20px;
        }
    }
    
}