
// search
.search {

    &__form {
        position: relative;
        display: flex;
        gap: 10px;
        margin-bottom: 40px;

        & input {
            width: 100%;
            max-width: 590px;
            padding-left: 50px;
        }

        &-icon {
            position: absolute;
            top: 50%;
            left: 20px;
            transform: translate(0%, -40%);
        }

        &-btn img {
            display: none;
        }
    }

    &__no-result {
        //display: none;
        font-family: "Cormorant Garamond", sans-serif;
        font-weight: 700;
        font-size: 40px;
        color: #212121;
    }

    &__result-title {
        font-family: "Cormorant Garamond", sans-serif;
        font-weight: 700;
        font-size: 40px;
        color: #212121;
        padding-bottom: 40px;
        margin-bottom: 40px;
        border-bottom: 1px solid #dbe1dd;
    }

    &__result-row {
        display: flex;
        justify-content: space-between;
        gap: 40px;
    }

    &__result-name {
        font-weight: 700;
        font-size: 20px;
        color: #424242;
        margin-bottom: 40px;
    }

    &__result-services {
        flex: 1 0 40%;
        & ul {
            display: flex;
            flex-direction: column;
            gap: 12px;
            & a {
                font-size: 16px;
                color: #996b3e;
                transition: all 0.3s ease;
                &:hover {
                    color: #424242;
                    text-decoration: underline;
                }
            }
        }
    }

    &__result-articles {
        flex: 1 0 40%;
        & ul {
            display: flex;
            flex-direction: column;
            gap: 20px;
            & a {
                display: block;
                padding: 30px;
                border-radius: 20px;
                background: #fbfbfb;
                color: #424242;
                display: flex;
                flex-direction: column;
                gap: 20px;
                border: 1px solid transparent;
                transition: all 0.3s ease;
                &:hover {
                    border: 1px solid #424242;
                }
            }
        }
    }

    &__result-article {
        font-weight: 500;
        color: #212121;
        padding-bottom: 20px;
        position: relative;
        &::after {
            content: "";
            display: block;
            width: 80px;
            height: 1px;
            background: #cc9600;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }
}

@media (max-width: 768px) {
    .search {

        &__form {
            position: relative;
            display: flex;
            gap: 10px;
            margin-bottom: 20px;
    
            & input {
                max-width: none;
                padding: 14px;
            }
    
            &-icon {
                display: none;
            }

            &-btn {
                flex-shrink: 0;
                aspect-ratio: 1 / 1;
                & span {
                    display: none;
                }
                & img {
                    display: block;
                }
            } 
        }
    
        &__no-result {
            font-size: 25px;
        }
    
        &__result-title {
            font-size: 25px;
            padding-bottom: 20px;
            margin-bottom: 20px;
        }
    
        &__result-row {
            flex-direction: column;
            gap: 40px;
        }
    
        &__result-name {
            font-size: 18px;
            margin-bottom: 20px;
        }
    
        &__result-services {
            & ul {
                & a {
                    font-size: 15px;
                }
            }
        }
    }
}