
// footer
.footer {
    padding: 100px 0 50px;
    background: #3d5648;

    &__block {
        padding-bottom: 50px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.3);
        margin-bottom: 50px;
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        margin-bottom: 50px;
    }

    &__logo p, &__logo span {
        color: #fff;
    }

    &__contacts {
        display: flex;
        align-items: center;
        gap: 40px;

        &-row {
            display: flex;
            align-items: center;
            gap: 40px;
        }
    }

    &__links {
        display: flex;
        flex-wrap: wrap;
        gap: 20px 40px;
        margin-bottom: 50px;

        & a {
            font-size: 16px;
            text-decoration: underline;
            text-decoration-skip-ink: none;
            color: #fff;
            display: flex;
            align-items: center;
            gap: 15px;

            &::before {
                content: "";
                display: block;
                width: 18px;
                height: 25px;
                background: url("./../img/icons/culc.svg") no-repeat center / contain;
                flex-shrink: 0;
            }
        }
    }

    &__nav {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        gap: 30px;

        &-item {
            & .accordion, .accordion__content {
                display: contents;
            }
        }

        &-title {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;
            font-weight: 500;
            color: #fff;
            & a {
                font-weight: 500;
                color: #fff;
            }
        }

        &-arrow {
            display: none;
        }

        &-list {
            display: flex;
            flex-direction: column;
            gap: 12px;
            padding-top: 20px;
            & a, & span {
                display: block;
                line-height: 120%;
                font-size: 14px;
                color: #fff;
            }
        }
    }

    &__midle {
        display: flex;
        justify-content: space-between;
        gap: 30px;
        font-size: 16px;
        line-height: 1.5;
        color: #fff;
        & a {
            color: #fff;
            text-decoration: underline;
        }
    }

    &__pay-row {
        margin-top: 15px;
        display: flex;
        gap: 5px;
    }

    &__pay-item {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 60px;
        height: 60px;
        border-radius: 12px;
        background: rgba(255, 255, 255, 0.1);
    }

    &__search {
        &-form {
            position: relative;
            margin-bottom: 20px;
        }

        &-input {
            border: 1px solid #fff;
            border-radius: 10px;
            padding: 13px 20px;
            padding-left: 50px;
            width: 100%;
            max-width: 380px;
            font-size: 14px;
            color: #fff;
            &::placeholder {
                font-size: 14px;
                color: #fff;
            }
        }

        &-icon {
            position: absolute;
            top: 50%;
            left: 18px;
            transform: translate(0%, -40%);
            
        }

        &-bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            & a {
                display: flex;
                gap: 10px;
                color: #a9a9a9;
                transition: all 0.3s ease;
                &:hover {
                    color: #fff;
                }
            }
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        font-size: 15px;
        line-height: 1.3;
        color: #a9a9a9;
    }

    &__legal {
        display: flex;
        gap: 20px;
        & a {
            font-size: 15px;
            line-height: 1.3;
            text-decoration: underline;
            color: #a9a9a9;
            transition: all 0.3s ease;
            &:hover {
                color: #fff;
            }
        }
    }

}

.scroll-top {
    position: fixed;
    bottom: 40px;
    right: 40px;
    z-index: 20;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    background: #fff;
    border: 2px solid #997000;
    transition: all 0.3s ease;
    opacity: 1;
    visibility: visible;
    pointer-events: all;
    
    & svg path {
        transition: all 0.3s ease;
    }
    &:hover {
        background: #997000;
        & svg path {
            fill: #fff;
        }
    }

    &.hidden {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
    }
}


@media (max-width: 1240px) {
    .footer {
        &__nav {
            grid-template-columns: repeat(3, 1fr);
            gap: 40px 30px;
        }
        &__midle {
            flex-wrap: wrap;
            justify-content: space-between;
            gap: 30px;
        }
    }
}
@media (max-width: 1024px) {
    .footer {
        &__logo {
            gap: 13px;
        }
        &__logo p {
            font-size: 32px;
        }
        &__logo span {
            display: block;
            font-size: 13px;
            letter-spacing: -0.05em;
        }
        &__top {
            flex-direction: column;
            align-items: stretch;
            gap: 30px;
        }
        &__contacts {
            justify-content: space-between;
        }
        &__bottom {
            flex-direction: column;
            align-items: stretch;
            gap: 20px;
        }
    }
}
@media (max-width: 768px) {
    .footer {
        padding: 40px 0;
    
        &__block {
            padding-bottom: 40px;
            margin-bottom: 40px;
        }
    
        &__top {
            margin-bottom: 40px;
        }
    
        &__contacts {
            flex-direction: column;
            align-items: stretch;
            gap: 20px;
    
            &-row {
                justify-content: space-between;
                gap: 20px;
            }
        }
    
        &__links {
            flex-direction: column;
            gap: 20px;
            margin-bottom: 40px;
    
            & a {
                font-size: 14px;
            }
        }
    
        &__nav {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
            &-item {
                & .accordion, .accordion__content {
                    display: block;
                }
            }
            &-arrow {
                display: block;
            }
        }
    
        &__midle {
            flex-direction: column;
            gap: 30px;
        }

        &__info {
            font-size: 13px;
        }
    
        &__pay-item {
            padding: 10px;
            height: 40px;
            max-width: 80px;
            flex-grow: 1;
        }
    
        &__search {
            &-input {
                width: 100%;
                max-width: 500px;
            }
            &-bottom {
                flex-direction: column-reverse;
                align-items: stretch;
                gap: 10px;
                & a {
                    font-size: 14px;
                }
                & svg {
                    width: 25px;
                }
            }
        }
    
        &__bottom {
            font-size: 14px;
        }
    
        &__legal {
            flex-direction: column;
            gap: 10px;
            & a {
                font-size: 14px;
            }
        }
    
    }
}