
// reviews
.reviews {
    background: rgba(86, 105, 46, 0.05);

    &__swiper-slide {
        opacity: 0;
        visibility: hidden;
        &.swiper-slide-active {
            opacity: 1;
            visibility: visible;
        }
    }

    &__swiper-btns {
        margin-top: 28px;
        margin-left: 400px;
    }

    &__cards {
        display: flex;
        flex-direction: column;
        gap: 40px;
    }

    &__card {
        display: flex;
        align-items: flex-start;
        gap: 60px;
        position: relative;

        &::after {
            content: "";
            display: block;
            width: 200px;
            height: 158px;
            background: url("./../img/icons/quote.svg") no-repeat center / contain;
            opacity: 0.08;
            position: absolute;
            top: 110px;
            right: 110px;
            transform: rotate(180deg);
        }
    }

    &__card-photo {
        display: block;
        width: 100%;
        max-width: 340px;
        min-width: 340px;
        background: #E8E8E8;
        border-radius: 20px;
        & img {
            border-radius: 20px;
            width: 100%;
            height: 100%;
            object-fit: cover;
            min-height: 300px;
            max-height: 318px;
        }
    }

    &__card-content {
        position: relative;
        z-index: 2;
    }

    &__card-stars {
        display: flex;
        gap: 5px;
        width: fit-content;
        position: relative;
        margin-bottom: 20px;
        &::after {
            content: "";
            display: block;
            width: 240px;
            height: 1px;
            background: #ca9561;
            position: absolute;
            top: 50%;
            right: -20px;
            transform: translate(100%, -50%);
            
        }
    }

    &__card-star {
        & svg path {
            fill: #d4d4d4;
        }
        &.active {
            & svg path {
                fill: #3d5648;
            }
        }
    }

    &__card-text {
        line-height: 1.55556;
        color: #212121;
        margin-bottom: 20px;
    }

    &__card-btn-more {
        display: none;
    }

    &__card-fio {
        font-weight: 500;
        font-size: 22px;
        color: #212121;
        margin-bottom: 10px;
    }

    &__card-date {
        font-size: 16px;
        color: #996b3e;
    }
}

.reviews-page {
    &__top {
        margin-bottom: 60px;
        &-btn {
            max-width: 300px;
        }
    }

    &__card {
        border: 1px solid #dbe1dd;
        border-radius: 20px;
        padding: 30px;
    }
}

@media (max-width: 1024px) {
    .reviews {
        &__swiper-btns {
            margin-top: 28px;
            margin-left: 300px;
        }
    
        &__card {
            gap: 40px;
            &::after {
                width: 160px;
                height: 120px;
                top: 90px;
                right: 60px;
            }
        }
    
        &__card-photo {
            max-width: 260px;
            min-width: 260px;
            & img {
                min-height: 260px;
                max-height: 280px;
            }
        }
    }
}

@media (max-width: 768px) {
    .reviews {
        position: relative;

        & .title-h2 {
            max-width: calc(100% - 90px);
            & span {
                display: none;
            }
        }
    
        &__swiper {
            position: static;
        }

        &__swiper-slide {
            height: auto !important;
        }
    
        &__swiper-btns {
            margin: 0;
            position: absolute;
            top: 80px;
            right: 20px;
        }
    
        &__card {
            flex-direction: column;
            align-items: stretch;
            gap: 30px;
            position: static;
            height: 100%;
            &::after {
                content: none;
            }
        }
    
        &__card-photo {
            width: 100%;
            min-width: none;
            max-width: 100%;
            & img {
                min-height: 270px;
                max-height: 270px;
                object-position: top;
            }
        }
    
        &__card-stars {
            margin-bottom: 10px;
            &::after {
                content: none;
                
            }
        }
    
        &__card-text {
            margin-bottom: 15px;
            max-height: 220px;
            overflow: hidden;
        }

        &__card-btn-more {
            display: block;
            margin-bottom: 30px;
            font-weight: 500;
            font-size: 16px;
            line-height: 1.5;
            text-decoration: underline;
            text-decoration-skip-ink: none;
            color: #212121;
        }
    
        &__card-fio {
            font-size: 18px;
        }
    
        &__card-date {
            font-size: 14px;
        }
    }
    
    .reviews-page {
        &__top {
            margin-bottom: 40px;
            &-btn {
                max-width: 100%;
            }
        }
    
        &__card {
            padding: 20px;
        }
    }
}