
// faq
.faq {
    background: url("./../img/bg-light-green.jpg") no-repeat center / cover;

    &__list {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__item {
        border-radius: 20px;
        padding: 30px;
        background: rgba(255, 255, 255, 0.75);
        &.accordion--active {
            box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.05);
            background: #fff;
            & .faq__item-btn {
                transform: rotate(45deg);
            }
        }
    }

    &__item-question {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        font-weight: 600;
        font-size: 22px;
        color: #212121;
    }

    &__item-btn {
        flex-shrink: 0;
        transition: all 0.3s ease;
    }

    &__item-answer {
        line-height: 1.5;
        color: #212121;
        padding: 30px 80px 0 0;
    }
}

.faq-page {
    & .faq__item {
        border: 1px solid #dbe1dd;
        &.accordion--active {
            border: 1px solid #56692e;
        }
    }
}

@media (max-width: 768px) {
    .faq {
        &__list {
            gap: 10px;
        }
    
        &__item {
            padding: 20px;
        }
    
        &__item-question {
            font-size: 16px;
        }
    
        &__item-answer {
            font-size: 15px;
            padding: 20px 0 0 0;
        }
    }
}