
// alcotest
.alcotest {

    &__wrap {
        // border-radius: 20px;
        // padding: 30px;
        // box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.05);
        // background: #fff;
        // background: rgba(86, 105, 46, 0.05);
        display: flex;
        gap: 60px;
    }

    &__content {
        width: 100%;
        max-width: 800px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 20px;
        
        &-block {
            display: flex;
            flex-wrap: wrap;
            gap: 20px;
        }

        &-item {
            flex: 1 0 40%;
            margin-top: auto;
            max-width: calc(50% - 10px);

            & label {
                display: block;
                font-size: 16px;
                color: #000;
                margin-bottom: 10px;
            }

            & button, & input {
                width: 100%;
            }
        }
    }

    &__picture {
        display: block;
        width: 70%;
        max-width: 580px;
        & img {
            border-radius: 10px;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}


@media (max-width: 1024px) {
    .alcotest {
        &__content {
            max-width: 100%;
        }
        &__picture {
            display: none;
        }
    }
}

@media (max-width: 768px) {
    .alcotest {
        &__form {
            &-item {
                & label {
                    font-size: 15px;
                }
            }
        }
    }
}

@media (max-width: 600px) {
    .alcotest {
        &__form {
            &-block {
                flex-direction: column;
            }
            &-item {
                max-width: 100%;
            }
        }
    }
}