
// prices
.prices {
    padding: 60px 0;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.05) 0%, rgba(86, 105, 46, 0.05) 47.6%);

    &__row {
        display: flex;
        gap: 60px;
    }

    &__left {
        flex: 1 0 40%;
    }

    &__inner {
        border-radius: 10px;
        padding: 60px 30px 50px;
        background: #dbe1dd;
        margin-bottom: 30px;
    }

    &__title {
        text-align: left;
    }

    &__tab-btn {
        padding: 30px;
        font-weight: 600;
        font-size: 20px;
        letter-spacing: 0.02em;
        color: #000;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        width: 100%;
        border-bottom: 1px solid rgba(86, 105, 46, 0.3);
        transition: all 0.3s ease;

        &::after {
            content: '';
            display: inline-block;
            width: 23px;
            height: 18px;
            background-color: currentColor; /* Наследует цвет текста */
            -webkit-mask: url('data:image/svg+xml;utf8,<svg width="23" height="18" viewBox="0 0 23 18" fill="none" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" clip-rule="evenodd" d="M23 9.00001C23 9.23099 22.9114 9.45257 22.7535 9.61585L14.8998 17.7449C14.5712 18.085 14.0385 18.085 13.7099 17.7449C13.3813 17.4048 13.3813 16.8534 13.7099 16.5132L20.127 9.87098H0.841463C0.37674 9.87098 0 9.48102 0 9.00001C0 8.51901 0.37674 8.12904 0.841463 8.12904H20.127L13.7099 1.48684C13.3813 1.1467 13.3813 0.595229 13.7099 0.255098C14.0385 -0.0850327 14.5712 -0.0850327 14.8998 0.255098L22.7535 8.38418C22.9114 8.54746 23 8.76903 23 9.00001Z" fill="black"/></svg>') no-repeat center;
            mask: url('data:image/svg+xml;utf8,<svg width="23" height="18" viewBox="0 0 23 18" fill="none" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" clip-rule="evenodd" d="M23 9.00001C23 9.23099 22.9114 9.45257 22.7535 9.61585L14.8998 17.7449C14.5712 18.085 14.0385 18.085 13.7099 17.7449C13.3813 17.4048 13.3813 16.8534 13.7099 16.5132L20.127 9.87098H0.841463C0.37674 9.87098 0 9.48102 0 9.00001C0 8.51901 0.37674 8.12904 0.841463 8.12904H20.127L13.7099 1.48684C13.3813 1.1467 13.3813 0.595229 13.7099 0.255098C14.0385 -0.0850327 14.5712 -0.0850327 14.8998 0.255098L22.7535 8.38418C22.9114 8.54746 23 8.76903 23 9.00001Z" fill="black"/></svg>') no-repeat center;
            mask-size: contain;
            -webkit-mask-size: contain;
        }
        &.tab-btn--active {
            border-radius: 10px;
            background: #fff;
        }

        &:nth-last-child(-n+2) {
            border-bottom: none;
        }
    }

    &__tab-link {
        border-radius: 10px;
        background: rgba(86, 105, 46, 0.1);
    }

    &__right {
        flex: 1 0 50%;
    }

    &__picture {
        display: block;
        margin-bottom: 50px;
        & img {
            border-radius: 10px;
            width: 100%;
            height: 360px;
            object-fit: cover;
        }
    }

    &__name {
        font-weight: 700;
        font-size: 20px;
        color: #996b3e;
        padding-bottom: 20px;
        margin-bottom: 20px;
        position: relative;
        &::after {
            content: "";
            display: block;
            width: 80px;
            height: 1px;
            background: #ca9561;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }

    &__table {
        & table {
            display: block;
            max-height: 444px;
            overflow-y: auto;
            padding-right: 16px;
            &::-webkit-scrollbar {
                width: 4px;
            }
            &::-webkit-scrollbar-track {
                background: #c5c4c1;
            }
            &::-webkit-scrollbar-thumb {
                background: #997000;
            }

            & tbody {
                display: flex;
                flex-direction: column;
                gap: 20px;
            }
            & tr {
                display: flex;
                justify-content: space-between;
                gap: 10px;
            }
            & td:first-child {
                flex: 1 0 70%;
                font-weight: 500;
                font-size: 18px;
                line-height: 1.33333;
            }
            & td:last-child {
                flex: 1 0 20%;
                font-weight: 400;
                font-size: 20px;
                line-height: 1.2;
                text-align: right;
            }
        }
    }
}

.prices-service {
    & .prices__tab-btn {
        &:not(:last-child) {
            border-bottom: 1px solid rgba(86, 105, 46, 0.3);
        }
    }
    & .prices__name {
        padding-top: 20px;
    }
}

@media (hover:hover) {
    .prices {
        &__tab-btn {
            &.tab-btn--active {
                &:hover {
                    background: #fff;
                }
            }
    
            &:hover {
                border-radius: 10px;
                background: rgba(86, 105, 46, 0.15);
            }
        }
    } 
}

@media (max-width: 1200px) {
    .prices {
        &__row {
            flex-direction: column;
            gap: 30px;
        }
    
        &__left {
            display: contents;
        }
    
        &__inner {
            border-radius: 0;
            padding: 0;
            background: transparent;
            margin-bottom: 0;
        }
    
        &__title {
            text-align: left;
        }
    
        &__tab-btns {
            display: flex;
            gap: 10px;
            max-width: 100%;
            overflow-x: auto;
            padding-bottom: 1px;
            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }
    
        &__tab-btn {
            border: 1px solid #dbe1dd !important;
            border-radius: 10px;
            padding: 16px 26px;
            background: #fff;
            font-weight: 600;
            font-size: 16px;
            min-width: max-content;
            justify-content: center;
    
            &::after {
                content: none;
            }
            &.tab-btn--active {
                background: #dbe1dd;
            }
        }

        &__tab-link {
            background: rgba(86, 105, 46, 0.1);
        }
    
        &__table table {
            padding: 0;
            max-height: none;
        }

        &__btn {
            order: 3;
        }
    } 
}
@media (max-width: 768px) {
    .prices {
        &__tab-btn {
            padding: 10px 20px;
        }
    
        &__picture {
            display: none;
        }
    
        &__name {
            padding-bottom: 15px;
            margin-bottom: 15px;
        }
    
        &__table {
            & table {
                & tbody {
                    gap: 15px;
                }
                & td:first-child {
                    flex: 1 0 60%;
                    font-size: 15px;
                }
                & td:last-child {
                    flex: 1 0 35%;
                    font-size: 18px;
                }
            }
        }
    } 
}


.prices-page {
    &__table {
        & table {
            max-height: 100%;
        }
        & .prices-page__item {
            border: 1px solid #dbe1dd;
            border-radius: 20px;
            padding: 20px;
            display: flex;
            align-items: center;
            gap: 20px;
            font-size: 20px;
            transition: all 0.3s ease;
            position: relative;
            &:hover {
                border: 1px solid #56692e;
            }
    
            & td:first-child {
                flex: 1 0 57%;
            }
            & td:nth-child(2) {
                flex: 1 0 16%;
            }
            & td:nth-child(3) {
                flex: 1 0 fit-content;
            }
            & button {
                margin-left: auto;
            }
        } 
    }

    &__pays {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 15px;
        font-size: 20px;
        margin-top: 40px;

        &-row {
            display: flex;
            gap: 5px;
        }

        &-item {
            border-radius: 12px;
            padding: 15px;
            background: rgba(61, 86, 72, 0.05);
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 5px;
            min-width: 112px;
            font-size: 15px;
        }
    }
}

@media (max-width: 1000px) {
    .prices-page {
        &__table {
            & .prices-page__item {
                & td:first-child {
                    flex: 1 0 45%;
                }
                & td:nth-child(2) {
                    flex: 1 0 18%;
                }
            } 
        }
        &__pays {
            flex-direction: column;
            align-items: stretch;
            &-item {
                flex: 1 0 10%;
                min-width: auto;
            }
        }
    }
}
@media (max-width: 768px) {
    .prices-page {
        &__table {
            & table {
                max-height: 100%;
            }
            & .prices-page__item {
                font-size: 18px;

                & td:first-child {
                    flex: 1 0 60%;
                }
                & td:nth-child(2) {
                    flex: 1 0 35%;
                    text-align: right;
                }
                & td:nth-child(3), & button {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    opacity: 0;
                }
            } 
        }

        &__pays {
            font-size: 15px;
            margin-top: 30px;
    
            &-row {
                flex-wrap: wrap;
            }
    
            &-item {
                flex: 1 0 25%;
                font-size: 13px;
            }
        }
    }
}