/* swiper CUSTOM */

.swiper {
    width: 100%;
    height: 100%;
}

/* swiper-buttons */
.swiper-btns {
    display: flex;
    align-items: center;
    gap: 6px;
}
.swiper-button-next, .swiper-button-prev {
    position: static;
    margin: 0;
    flex-shrink: 0;
    width: 58px;
    height: 58px;
    border-radius: 10px;
    background: #56692e;
    border: 1.5px solid #56692e;
    transition: all 0.3s ease;
}
.swiper-button-next:after, .swiper-button-prev:after {
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    transition: all 0.3s ease;
}
.swiper-button-next:hover,.swiper-button-prev:hover {
    background: #fff;
}
.swiper-button-next:hover:after, .swiper-button-prev:hover:after {
    color: #56692e;
}


/* swiper-buttons--white */
.swiper-button-next--white, .swiper-button-prev--white {
    background: var(--belyy);
}
.swiper-button-next--white:after, .swiper-button-prev--white:after {
    color: var(--color-temno-zelenyy);
}
.swiper-button-next--white:hover,.swiper-button-prev--white:hover {
    background: rgba(61, 124, 133, 0.25);
}
.swiper-button-next--white:hover:after, .swiper-button-prev--white:hover:after {
    color: var(--color-temno-zelenyy);
}


/* swiper-pagination  */
.swiper-pagination {
    position: static;
    width: auto !important;
    margin-top: 32px;

    &-bullet {
        position: static;
        border-radius: 50px;
        width: 20px;
        height: 10px;
        background: #bcbbbb;
        transition: all 0.3s ease;
    }
    
    &-bullet-active {
        width: 40px;
        background: #997000;
    }
}


@media (max-width: 768px) {
    .swiper-button-next, .swiper-button-prev {
        width: 36px;
        height: 36px;
        border-radius: 10px;
        background: #56692e;
        border: 1.5px solid #56692e;
        transition: all 0.3s ease;
        &::after {
            font-size: 16px;
        }
    }
    .swiper-pagination {
        margin-top: 20px;
    }
}




.swiper-pagination-fraction {
    font-weight: 700;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.5);

    .swiper-pagination-current {
        font-weight: 700;
        font-size: 26px;
        color: var(--belyy);
    }
}

.swiper-scrollbar {
    position: static !important;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    &-drag {
        background: var(--belyy);
        border-radius: 10px;
    }
}

