
// licenses
.licenses {
    &__swiper {
        margin-bottom: 50px;
    }

    &__swiper-slide {
        height: auto;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        gap: 60px 20px;
        & .licenses__item-picture img {
            max-height: 400px;
        }
    }

    &__item {
        display: block;
        width: 100%;
        height: 100%;
    }

    &__item-picture {
        display: block;
        width: 100%;
        //height: 100%;
        & img {
            width: 100%;
            height: 100%;
            max-height: 332px;
            object-fit: cover;
            object-position: top;
            border-radius: 4px;
        }
    }

    &__item-name {
        padding: 20px 0;
        font-weight: 500;
        font-size: 16px;
        line-height: 1.5;
        color: #996b3e;
        position: relative;
        &::after {
            content: "";
            display: block;
            width: 80px;
            height: 1px;
            background: #cc9600;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }

    &__swiper-pagination {
        display: none;
    }

    &__btn {
        width: fit-content;
        margin: 0 auto;
    }
}

@media (max-width: 1200px) {
    .licenses {
        &__cards {
            grid-template-columns: repeat(4, 1fr);
        }
    }
}
@media (max-width: 940px) {
    .licenses {
        &__cards {
            grid-template-columns: repeat(3, 1fr);
            gap: 40px 20px;
        }
    }
}
@media (max-width: 768px) {
    .licenses {
        overflow: hidden;

        &__swiper {
            overflow: visible;
            margin-bottom: 40px;
        }

        &__swiper-btns {
            display: none;
        }

        & .title-h2 {
            text-align: center;
        }
    
        &__item-picture {
            & img {
                max-height: 380px;
            }
        }

        &__item-name {
            font-size: 14px;
            line-height: 1.3;
        }
    
        &__swiper-pagination {
            display: block;
        }

        &__btn {
            width: 100%;
        }
    }
}
@media (max-width: 600px) {
    .licenses {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 40px 10px;
        }
    }
}