
// articles
.articles {
    overflow: hidden;

    &__swiper {
        margin-bottom: 50px;
    }

    &__swiper-slide {
        height: auto;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 40px 20px;
    }

    &__card {
        display: flex;
        flex-direction: column;
        height: 100%;
        color: #424242;
        border: 1px solid transparent;
        border-radius: 20px;
        background: #fbfbfb;
        transition: all 0.3s ease;
        &:hover {
            border: 1px solid #997000;
        }
    }

    &__card-picture {
        display: block;
        width: 100%;
        & img {
            width: 100%;
            height: 239px;
            border-radius: 20px;
            object-fit: cover;
        }
    }

    &__card-content {
        padding: 30px;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
    }

    &__card-date {
        margin-bottom: 20px;
    }

    &__card-title {
        font-weight: 500;
        color: #212121;
        margin-bottom: 20px;
    }

    &__card-texts {
        flex-grow: 1;
        padding-bottom: 20px;
        margin-bottom: 20px;
        position: relative;
        &::after {
            content: "";
            display: block;
            width: 80px;
            height: 1px;
            background: #cc9600;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }
    &__card-text {
        font-size: 16px;
        line-height: 1.5;
        display: -webkit-box;
        -webkit-line-clamp: 5;
        -webkit-box-orient: vertical;
        overflow: hidden;
        max-height: calc(1.5em * 5); 
    }

    &__swiper-pagination {
        display: none;
    }
    &__btn {
        width: fit-content;
        margin: 0 auto;
    }
}

@media (max-width: 1200px) {
    .articles {
        &__cards {
            grid-template-columns: repeat(3, 1fr);
            gap: 40px 20px;
        }
    }
}
@media (max-width: 1000px) {
    .articles {
        &__swiper {
            overflow: visible;
        }
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 40px 20px;
        }
    }
}
@media (max-width: 768px) {
    .articles {
        &__swiper {
            margin-bottom: 40px;
        }
        &__card-picture {
            & img {
                height: 180px;
            }
        }
        &__card-content {
            padding: 20px;
        }
        &__swiper-pagination {
            display: block;
        }
        &__btn {
            width: 100%;
        }
    }
}
@media (max-width: 600px) {
    .articles {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }
    }
}