
// form

.form {
    &__bg {
        padding: 60px 0 120px;
        background: rgba(86, 105, 46, 0.05);
    }
    &__bg-green {
        padding: 120px 0;
        background: url("./../img/form/form-bg.jpg") no-repeat center / cover;
    }

    &__row {
        border-radius: 20px;
        padding: 30px;
        box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.05);
        background: #fff;
        display: flex;
        gap: 60px;
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 40px;
        max-width: 800px;
    }

    &__title {
        margin-bottom: 20px;
    }

    &__subtitle {
        font-weight: 500;
        font-size: 22px;
        color: #212121;
        line-height: 120%;
        margin-bottom: 20px;
    }

    &__text {
        font-size: 16px;
        color: #212121;
    }

    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        & div:not([class]) {
            display: none;
        }
    }

    &__input, &__btn, &__politic {
        flex: 1 0 40%;
    }

    &__img {
        display: block;
        width: 100%;
        max-width: 580px;
        & img {
            border-radius: 10px;
            width: 100%;
            height: 100%;
            max-width: 580px;
            object-fit: cover;
        }
    }
}

@media (max-width: 1130px) {
    .form {
        &__row {
            flex-direction: column-reverse;
            gap: 40px;
        }
        &__content {
            max-width: 100%;
        }
        &__img {
            max-width: 100%;
            & img {
                max-width: 100%;
                max-height: 400px;
            }
        }
    }
}
@media (max-width: 768px) {
    .form {
        &__bg {
            padding: 40px 0 80px;
        }
    
        &__row {
            border-radius: 20px;
            padding: 20px;
        }
    
        &__content {
            gap: 30px;
        }
        &__title {
            font-size: 25px;
            line-height: 120%;
        }
        &__subtitle {
            font-size: 18px;
        }
        &__text {
            font-size: 14px;
        }
    
        &__wrapper {
            flex-direction: column;
            gap: 10px;
        }
    
        &__input, &__btn {
            flex: 1 0 40%;
        }
        &__politic {
            margin-top: 10px;
        }
    
        &__img {
            & img {
                max-height: 260px;
            }
        }
    }
}