
// contacts
.contacts {

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 60px;
    }

    &__left {
        flex: 1 0 50%;
    }

    &__info {
        margin-bottom: 60px;
    }

    &__title {
        font-family: "Cormorant Garamond", sans-serif;
        font-weight: 700;
        font-size: 24px;
        text-transform: uppercase;
        color: #424242;
        margin-bottom: 30px;
    }

    &__info-wrap {
        display: flex;
        flex-wrap: wrap;
        gap: 40px;
    }

    &__info-item {
        flex: 1 0 40%;
        display: flex;
        align-items: center;
        gap: 20px;
    }

    &__info-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 60px;
        height: 60px;
        border-radius: 10px;
        background: rgba(61, 86, 72, 0.05);
        flex-shrink: 0;
    }

    &__info-text {
        & span {
            display: block;
            color: #212121;
        }
        & p, & a, & button {
            font-weight: 600;
            color: #996b3e;
            transition: all 0.3s ease;
        }
        & a:hover, & button:hover {
            color: #56692e;
            text-decoration: underline;
        }
    }

    &__lic {
        border-radius: 20px;
        padding: 30px;
        background: #fbfbfb;
        & p:not(:last-child) {
            margin-bottom: 15px;
        }
    }

    &__right {
        flex: 1 0 40%;
    }

    &__maps {
        border-radius: 20px;
        width: 100%;
        height: 100%;
        min-height: 700px;
        position: relative;
        overflow: hidden;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }
}

@media (max-width: 1024px) {
    .contacts {
        &__row {
            flex-direction: column;
            gap: 40px;
        }
    
        &__left {
            display: contents;
        }
    
        &__info {
            margin-bottom: 0;
        }
    
        &__lic {
            order: 3;
        }
    
        &__right {
            flex: 1 0 40%;
        }
    
        &__maps {
            min-height: 500px;
        }
    }
}

@media (max-width: 700px) {
    .contacts {
        &__title {
            font-size: 20px;
            margin-bottom: 20px;
        }
    
        &__info-wrap {
            flex-direction: column;
            gap: 20px;
        }
    
        &__info-item {
            align-items: flex-start;
            gap: 20px;
        }
    
        &__info-icon {
            width: 45px;
            height: 45px;
        }
    
        &__info-text {
            font-size: 15px;
            & a[href^="tel:"]  {
                font-size: 18px;
            }
        }
    
        &__lic {
            padding: 20px;
            & p:not(:last-child) {
                margin-bottom: 10px;
            }
        }

        &__maps {
            min-height: 360px;
        }
    }
}