
// intro
.intro-about {

    &__wrap {
        display: flex;
        border-radius: 20px;
        background: #fbfbfb;
    }

    &__picture {
        display: block;
        width: 100%;
        max-width: 50%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 20px;
        }
    }

    &__contant {
        padding: 60px;
        width: 100%;
        max-width: 50%;
    }

    &__text {
        font-size: 20px;
        line-height: 1.5;
        color: #212121;
        & a {
            color: #212121;
            text-decoration: underline;
        }
        & p:not(:last-child) {
            margin-bottom: 20px;
        }
    }
}

@media (max-width: 1100px) {
    .intro-about {
        &__wrap {
            flex-direction: column;
        }
    
        &__picture {
            max-width: 100%;
            & img {
                max-height: 400px;
                object-position: top;
            }
        }
    
        &__contant {
            max-width: 100%;
        }
    }
}
@media (max-width: 768px) {
    .intro-about {
        &__picture {
            & img {
                min-height: 200px;
                max-height: 280px;
            }
        }
    
        &__contant {
            padding: 40px 10px 20px;
        }
    
        &__text {
            font-size: 16px;
        }
    }
    
}