
// error

.error {
    &__top {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 80px;
        padding: 70px 0;
        margin-bottom: 60px;
        &::after {
            background: #dfe1e0;
        }
    }

    &__num {
        font-weight: 700;
        font-size: 250px;
        color: #3d5648;
        line-height: 100%;
    }

    &__text {
        max-width: 573px;
    }

    &__title {
        font-family: "Cormorant Garamond", sans-serif;
        font-weight: 700;
        font-size: 24px;
        text-transform: uppercase;
        color: #424242;
        margin-bottom: 10px;
    }

    &__sutitle {
        font-size: 16px;
        line-height: 1.5;
        margin-bottom: 40px;
    }

    &__btn {
        width: fit-content;
    }

    &__services {
        width: 100%;
        max-width: 895px;
        margin: 0 auto;

        & p {
            font-weight: 500;
            font-size: 22px;
            text-align: center;
            color: #212121;
            margin-bottom: 40px;
        }

        & ul {
            display: flex;
            flex-wrap: wrap;
            gap: 20px;

            & li {
                flex:  1 0 30%;
            }

            & a {
                display: block;
                border: 1px solid #dfe1e0;
                border-radius: 10px;
                padding: 18px 40px;
                font-weight: 700;
                font-size: 16px;
                text-align: center;
                color: #56692e;
                transition: all 0.3s ease;
                &:hover {
                    background: #56692e25;
                    
                }
            }
        }
    }
}

@media (max-width: 1024px) {
    .error {
        &__top {
            flex-direction: column;
            gap: 40px;
        }
        &__text {
            text-align: center;
        }
        &__btn {
            width: 100%;
        }
    }
}

@media (max-width: 768px) {
    .error {
        &__top {
            gap: 20px;
            padding: 50px 0;
            margin-bottom: 50px;
        }
    
        &__num {
            font-size: 120px;
        }
        &__title {
            font-size: 20px;
        }
        &__sutitle {
            font-size: 15px;
        }
        &__services {
            & p {
                font-size: 18px;
                margin-bottom: 30px;
            }
            & ul {
                gap: 10px;
                & li {
                    flex:  1 0 40%;
                }
            }
        }
    }
}
@media (max-width: 500px) {
    .error {
        &__services {
            & ul {
                flex-direction: column;
            }
        }
    }
}