
// sitemap
.sitemap {

    &__top {
        padding-bottom: 40px;
        border-bottom: 1px solid #dbe1dd;
        margin-bottom: 40px;
        & ul {
            display: flex;
            flex-wrap: wrap;
            gap: 100px;
            & a {
                font-weight: 700;
                font-size: 20px;
                color: #424242;
                transition: all 0.3s ease;
                &:hover {
                    color: #56692e;
                }
            }
        }
    }

    &__main {
        padding-bottom: 40px;
        border-bottom: 1px solid #dbe1dd;
        margin-bottom: 40px;
    }

    &__main-list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 40px;
    }

    &__main-title {
        display: block;
        font-weight: 500;
        font-size: 18px;
        color: #424242;
        margin-bottom: 20px;
        transition: all 0.3s ease;
        &:hover {
            color: #56692e;
        }
    }

    &__main-sublist {
        display: flex;
        flex-direction: column;
        gap: 12px;
        & a {
            font-size: 16px;
            color: #996b3e;
            transition: all 0.3s ease;
            &:hover {
                color: #56692e;
                text-decoration: underline;
            }
        }
    }

    &__bottom {
        & ul {
            display: flex;
            flex-wrap: wrap;
            gap: 20px 40px;
            & a {
                font-size: 16px;
                text-decoration: underline;
                text-decoration-skip-ink: none;
                color: #424242;
                display: flex;
                align-items: center;
                gap: 15px;    
                transition: all 0.3s ease;

                &::before {
                    content: "";
                    display: block;
                    width: 18px;
                    height: 25px;
                    background: url("./../img/icons/culc-black.svg") no-repeat center / contain;
                    flex-shrink: 0;
                }

                &:hover {
                    color: #996b3e;
                }
            }
        }
    }
}

@media (max-width: 1024px) {
    .sitemap {
        &__top {
            & ul {
                gap: 40px;
            }
        }
        &__main-list {
            grid-template-columns: repeat(2, 1fr);
            gap: 40px;
        }
    }
}
@media (max-width: 620px) {
    .sitemap {
        &__top {
            & ul {
                flex-direction: column;
                gap: 20px;
                & a {
                    font-size: 18px;
                }
            }
        }
    
        &__main-list {
            grid-template-columns: repeat(1, 1fr);
            gap: 40px;
        }
    
        &__main-sublist {
            & a {
                font-size: 14px;
            }
        }
    
        &__bottom {
            & ul {
                flex-direction: column;
                gap: 20px;
                & a {
                    font-size: 14px;
                }
            }
        }
    }    
}