
// facts
.facts {
    &__list {
        & ul {
            display: flex;
            gap: 20px;
            & li {
                flex: 1 0 20%;
                border: 1px solid #dbe1dd;
                border-radius: 10px;
                padding: 100px 30px 30px;
                position: relative;
                margin-top: 63px;
            }
        }

        &-number {
            font-family: "Cormorant Garamond", sans-serif;
            font-weight: 700;
            font-size: 45px;
            letter-spacing: -0.02em;
            text-align: center;
            color: #fff;

            width: 150px;
            height: 126px;
            display: flex;
            align-items: center;
            justify-content: center;
            background: #56692e;
            -webkit-mask-image: url('data:image/svg+xml;utf8,<svg width="150" height="126" viewBox="0 0 150 126" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M101.643 123.161C123.199 114.254 140.85 94.935 147.31 71.6335C153.772 48.3303 148.457 21.5588 132.83 3.64001C131.426 2.02986 129.799 0.407496 127.741 0.0666738C125.106 -0.366855 122.66 1.39708 120.442 2.94596C109.035 10.9106 95.6972 15.2165 82.4546 18.9366C69.2121 22.6583 55.7341 25.9415 43.3866 32.1825C31.0407 38.4252 19.6966 48.0489 14.1081 61.1568C9.29783 72.4395 9.61498 86.514 16.0351 96.6273C10.6884 100.839 5.77947 105.664 1.4187 110.98C-0.667611 113.523 -0.426385 117.346 1.96771 119.572C1.99865 119.601 2.0294 119.629 2.06034 119.658C4.66868 122.084 8.7179 121.606 10.7301 118.623L21.6439 104.385C38.8119 119.295 80.7032 131.813 101.643 123.161Z" fill="white"/></svg>');
            mask-image: url('data:image/svg+xml;utf8,<svg width="150" height="126" viewBox="0 0 150 126" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M101.643 123.161C123.199 114.254 140.85 94.935 147.31 71.6335C153.772 48.3303 148.457 21.5588 132.83 3.64001C131.426 2.02986 129.799 0.407496 127.741 0.0666738C125.106 -0.366855 122.66 1.39708 120.442 2.94596C109.035 10.9106 95.6972 15.2165 82.4546 18.9366C69.2121 22.6583 55.7341 25.9415 43.3866 32.1825C31.0407 38.4252 19.6966 48.0489 14.1081 61.1568C9.29783 72.4395 9.61498 86.514 16.0351 96.6273C10.6884 100.839 5.77947 105.664 1.4187 110.98C-0.667611 113.523 -0.426385 117.346 1.96771 119.572C1.99865 119.601 2.0294 119.629 2.06034 119.658C4.66868 122.084 8.7179 121.606 10.7301 118.623L21.6439 104.385C38.8119 119.295 80.7032 131.813 101.643 123.161Z" fill="white"/></svg>');
            mask-repeat: no-repeat;
            mask-size: contain;
            -webkit-mask-repeat: no-repeat;
            -webkit-mask-size: contain;
            text-align: center;
            position: absolute;
            left: 50%;
            top: 0;
            transform: translate(-50%, -50%);
            
        }

        &-title {
            font-family: "Cormorant Garamond", sans-serif;
            font-weight: 700;
            font-size: 24px;
            text-transform: uppercase;
            text-align: center;
            color: #424242;
            margin-bottom: 20px;
        }

        &-text {
            text-align: center;
            font-size: 16px;
        }
    }

    &__subtitle {
        font-size: 22px;
        line-height: 1.36364;
        text-align: center;
        margin-top: 50px;
        margin-bottom: 0;
    }
}

@media (max-width: 1200px) {
    .facts {
        &__list {
            & ul {
                flex-wrap: wrap;
                & li {
                    flex: 1 0 40%;
                }
            }
        }
    }  
}

@media (max-width: 768px) {
    .facts {
        &__list {
            & ul {
                gap: 10px;
                & li {
                    border-radius: 10px;
                    padding: 60px 30px 30px;
                    margin-top: 36px;
                }
            }
    
            &-number {
                font-size: 30px;
                width: 90px;
                height: 76px;
            }
    
            &-title {
                font-size: 20px;
                margin-bottom: 10px;
            }
    
            &-text {
                font-size: 15px;
            }
        }
        &__subtitle {
            font-size: 16px;
            margin-top: 40px;
            margin-bottom: 0;
        }
    }
}
@media (max-width: 550px) {
    .facts {
        &__list {
            & ul {
                & li {
                    flex: 1 0 100%;
                }
            }
        }
    }  
}