
// stocks
.stocks {
    background: url("./../img/bg-dark-green.jpg") no-repeat center / cover;

    & .title-h2 {
        color: #fff;
        max-width: 600px;
    }

    &__cards {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
    }

    &__card {
        flex: 1 0 30%;
        border-radius: 20px;
        padding: 30px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
        background: #fff;
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    &__card-text {
        flex-grow: 1;
    }

    &__card-title {
        font-family: "Cormorant Garamond", sans-serif;
        font-weight: 700;
        font-size: 28px;
        line-height: 110%;
        text-transform: uppercase;
        text-align: center;
        color: #424242;
        margin-bottom: 6px;
        & span {
            color: #997000;
        }
    }

    &__card-subtitle {
        text-align: center;
        color: #424242;
    }

    &__card-picture {
        & img {
            border-radius: 10px;
            width: 100%;
            height: 284px;
            object-fit: cover;
        }
    }
}

.stocks-page {

    &__cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 30px 20px;
    }

    &__card {
        border: 1px solid rgba(86, 105, 46, 0.3);
        border-radius: 20px;
        padding: 30px;
        display: flex;
        gap: 30px;
    }

    &__card-picture {
        display: block;
        flex: 1 0 40%;
        & img {
            border-radius: 10px;
            width: 100%;
            height: 100%;
            min-height: 270px;
            object-fit: cover;
        }
    }

    &__card-content {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        gap: 40px;
        flex: 1 0 40%;
    }

    &__card-title {
        font-family: "Cormorant Garamond", sans-serif;
        font-weight: 700;
        font-size: 28px;
        text-transform: uppercase;
        color: #424242;
        line-height: 120%;
        margin-bottom: 10px;
        & span {
            color: #997000;
        }
    }
}

@media (max-width: 1140px) {
    .stocks-page {
        &__card {
            flex-direction: column;
            gap: 30px;
        }
    
        &__card-picture {
            flex: 0 1 auto;
            & img {
                min-height: 240px;
                max-height: 240px;
            }
        }
    
        &__card-content {
            display: contents;
        }

        &__card-text {
            order: -1;
            text-align: center;
            flex-grow: 1;
        }
    }
}

@media (max-width: 1024px) {
    .stocks {
        &__card {
            flex: 1 0 40%;
        }
    }
}

@media (max-width: 768px) {
    .stocks {
        & .title-h2 {
            text-align: center;
            max-width: 100%;
        }
    
        &__card {
            padding: 20px;
            gap: 20px;
        }

        &__card-title {
            font-size: 24px;
        }

        &__card-picture {
            & img {
                height: 173px;
            }
        }
    }

    .stocks-page {   
        &__card {
            padding: 20px;
        }
        &__card-picture {
            & img {
                min-height: 140px;
                max-height: 140px;
            }
        }
        &__card-title {
            font-size: 24px;
        }
    }
}

@media (max-width: 680px) {
    .stocks-page {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 600px) {
    .stocks {
        &__cards {
            flex-direction: column;
        }
    }
}