/* popups */
.popup {
    height: 100vh;
    left: 50%;
    opacity: 0;
    pointer-events: none;
    position: fixed;
    top: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
    width: 100%;
    z-index: 1000;
    -webkit-transition: opacity 0.4s ease;
    -o-transition: opacity 0.4s ease;
    transition: opacity 0.4s ease;
    overflow-y: auto;
    background: rgba(0, 0, 0, 0.7);
}

.popup__body {
    width: 100%;
    min-height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    padding: 20px;
}

.popup.open {
    opacity: 1;
    pointer-events: all;
}

.close-popup {
    cursor: url("./../img/cursor-pointer.png"), pointer;
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 1000;
    width: 18px;
    height: 18px;
    transition: transform 0.4s linear;
}
.close-popup img {
    height: 100%;
}
.close-popup:hover {
    transform: scale(1.2);
}


.popup__content {
    position: relative;
    width: 100%;
    max-width: 800px;
    border-radius: 20px;
    padding: 60px;
    background: #fff;
    transition: transform 0.3s linear;
}

.popup__title {
    margin-bottom: 20px;
}
.popup__subtitle {
    font-weight: 500;
    font-size: 22px;
    color: #212121;
    margin-bottom: 40px;
}

.popup-response {
    &__wrapper {
        & h3 {
            font-family: "Cormorant Garamond", sans-serif;
            font-weight: 700;
            font-size: 40px;
            text-transform: uppercase;
            color: #212121;
            line-height: 120%;
            max-width: 620px;
            margin-bottom: 20px;
        }
        & p {
            font-weight: 500;
            font-size: 22px;
            color: #212121;
            margin-bottom: 40px;
        }
    }
    &__btn {
        width: 100%;
        max-width: 330px;
    }
}


@media (max-width: 700px) {
    .popup__content {
        padding: 40px 20px;
    }
    .popup__title {
        font-size: 25px;
    }
    .popup__subtitle {
        font-size: 18px;
        margin-bottom: 30px;
    }
    .close-popup {
        top: 17px;
        right: 17px;
    }

    .popup-response {
        &__wrapper {
            & h3 {
                font-size: 25px;
            }
            & p {
                font-size: 18px;
                margin-bottom: 30px;
            }
        }
        &__btn {
            max-width: 100%;
        }
    }
}





.popup__search {

    &_form {
        position: relative;
    }

    &_input {
        background: rgba(234, 237, 242, 0.8);
        &:active, &:focus {
            background: rgba(234, 237, 242, 0.8);
        }
    }

    &_img {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
    }
}

.popup__search {
    &::-webkit-scrollbar {
        width: 4px;
        background-color: var(--izobrazhenie);
        padding: 5px;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background: var(--color-zelenyy);
    }
}

.popup__search-result {
    margin-top: 0;
    max-height: 370px;
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: 4px;
        background-color: var(--izobrazhenie);
        padding: 5px;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background: var(--color-zelenyy);
    }
}

.no-results-message {
    font-size: 16px;
    line-height: 140%;
    color: #ff4a37;
    padding: 16px;
    border-radius: 0 0 5px 5px;
    background: var(--fon-plashek);
    display: flex;
    gap: 10px;
    align-items: center;
}

.none {
    display: none;
}


