
// culc
.culc {
    padding: 60px 0 120px;
    background: rgba(86, 105, 46, 0.05);

    &__wrap {
        border-radius: 20px;
        padding: 30px;
        box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.05);
        background: #fff;
        display: flex;
        gap: 60px;
    }

    &__content {
        width: 100%;
        max-width: 800px;
    }

    &__title {
        margin-bottom: 20px;
    }

    &__subtitle {
        font-weight: 500;
        font-size: 22px;
        color: #212121;
        margin-bottom: 40px;
    }

    &__form {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;

        &-item {
            flex: 1 0 40%;
            margin-top: auto;

            & label {
                display: block;
                font-size: 16px;
                color: #000;
                margin-bottom: 10px;
            }

            & button {
                width: 100%;
            }
        }
    }

    &__picture {
        display: block;
        width: 70%;
        max-width: 580px;
        & img {
            border-radius: 10px;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}


@media (max-width: 1160px) {
    .culc {
        &__wrap {
            flex-direction: column-reverse;
            gap: 40px;
        }
        &__content {
            max-width: 100%;
        }
        &__picture {
            width: 100%;
            max-width: 100%;
            & img {
                max-height: 400px;
            }
        }
    }
}

@media (max-width: 768px) {
    .culc {
        padding: 40px 0 80px;
    
        &__wrap {
            padding: 20px;
        }
    
        &__title {
            font-size: 25px;
        }
    
        &__subtitle {
            font-size: 18px;
        }
    
        &__form {
            &-item {
                & label {
                    font-size: 15px;
                }
            }
        }
        &__picture {
            & img {
                max-height: 300px;
            }
        }
    }
}

@media (max-width: 600px) {
    .culc {
        &__form {
            flex-direction: column;
        }
        &__picture {
            & img {
                min-height: 260px;
                max-height: 260px;
            }
        }
    }
}