
// gallery
.gallery {
    overflow: hidden;

    &__swiper {
        margin-bottom: 50px;
    }

    &__swiper-slide {
        height: auto;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;

        & .gallery__item:nth-child(6n+1),
        & .gallery__item:nth-child(6n+3),
        & .gallery__item:nth-child(6n+4) {
            grid-row: span 10;
        }
    }

    &__item {
        display: block;
        width: 100%;
        height: 100%;
    }

    &__item-picture {
        display: block;
        width: 100%;
        height: 100%;
        & img {
            width: 100%;
            height: 100%;
            max-height: 320px;
            min-height: 280px;
            object-fit: cover;
            object-position: top;
            border-radius: 20px;
        }
    }

    &__swiper-pagination {
        display: none;
    }

    &__btn {
        width: fit-content;
        margin: 0 auto;
    }
}

.gallery-about {
    &__subtitle {
        font-weight: 500;
        font-size: 22px;
        line-height: 1.36364;
        color: #212121;
        margin-bottom: 50px;
        & p:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    &__swiper {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
        margin-bottom: 50px;
        & .swiper-slide, & .swiper-wrapper {
            display: contents;
        }
    }

    & .swiper-slide:nth-child(1) .gallery__item {
        grid-row: span 10;
    }
    & .swiper-slide:nth-child(3) .gallery__item {
        grid-row: span 10;
    }
    & .swiper-slide:nth-child(4) .gallery__item {
        grid-row: span 10;
    }
    
    & .gallery__item-picture {
        & img {
            height: 100%;
            max-height: 280px;
            max-height: 600px;
            min-height: 280px;
        }
    }
}

@media (max-width: 1100px) {
    .gallery {
        &__swiper {
            overflow: visible;
        }
    }
}

@media (max-width: 1024px) {
    .gallery-about {
        overflow: hidden;
        &__swiper {
            display: block;
            overflow: visible;
            & .swiper-wrapper {
                display: flex;
            }
            & .swiper-slide {
                display: block;
            }
        }
        & .swiper-slide:nth-child(n) .gallery__item {
            grid-row: span 1;
        }
    }

    .gallery {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
    
            & .gallery__item:nth-child(n) {
                grid-row: span 1;
            }
            & .gallery__item-picture img {
                max-height: 340px;
            }
        }

    }
}

@media (max-width: 768px) {
    .gallery {

        &__swiper {
            margin-bottom: 40px;
        }

        & .title-h2 span {
            display: none;
        }
    
        &__item-picture {
            & img {
                max-height: 340px;
                min-height: 300px;
            }
        }
    
        &__swiper-pagination {
            display: block;
        }

        &__btn {
            width: 100%;
        }
    }

    .gallery-about {
        &__subtitle {
            display: none;
        }
        &__swiper {
            margin-bottom: 40px;
        }
        
        & .gallery__item-picture {
            & img {
                max-height: 340px;
                min-height: 300px;
            }
        }
    }
}

@media (max-width: 600px) {
    .gallery {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
            & .gallery__item-picture img {
                min-height: 200px;
                max-height: 300px;
            }
        }

    }
}