
// banner
.banner {
    padding: 150px 0;
    position: relative;

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 50px;
        position: relative;
        z-index: 2;
    }

    &__texts {
        max-width: 50%;
    }

    &__title {
        margin-bottom: 10px;
    }

    &__subtitle {
        font-family: "Cormorant Garamond", sans-serif;
        font-weight: 700;
        font-size: 30px;
        text-transform: uppercase;
        color: #424242;
        line-height: 110%;
        margin-bottom: 30px;
    }

    &__text {
        border-radius: 20px;
        padding: 30px;
        background: rgba(255, 255, 255, 0.5);
        text-shadow: 0 4px 4px 0 rgba(255, 255, 255, 0.5);
        margin-bottom: 50px;
        & p:not(:last-child) {
            margin-bottom: 10px;
        }
    }

    &__btns {
        display: flex;
        gap: 10px;
    }

    &__list {
        max-width: 40%;
        & ol {
            display: flex;
            flex-direction: column;
            gap: 20px;
            list-style-type: none;
            counter-reset: item;

            & li {
                border-radius: 15px;
                padding: 30px;
                backdrop-filter: blur(50px);
                background: rgba(255, 255, 255, 0.2);
                font-family: "Gilroy", sans-serif;
                font-weight: 500;
                position: relative;
                padding-left: 140px;

                &::before {
                    content: "";
                    display: block;
                    width: 80px;
                    height: 80px;
                    background: url("./../img/icons/gear.svg") no-repeat center / contain;
                    position: absolute;
                    left: 30px;
                    top: 34px;
                    
                }

                & p {
                    font-weight: 700;
                    font-size: 24px;
                    color: #424242;
                    margin-bottom: 10px;
                    &::before {
                        content: counter(item) ". ";
                        counter-increment: item;
                    }
                }
                & span {
                    display: block;
                    max-width: 400px;
                }
            }
        }
    }
}

@media (max-width: 1200px) {
    .banner {
        padding: 100px 0;
    
        &__row {
            flex-direction: column;
            align-items: stretch;
            gap: 0px;
        }
    
        &__texts {
            display: contents;
        }

        &__subtitle {
            font-size: 24px;
        }
    
        &__btns {
            order: 3;
            & a, & button {
                flex-grow: 1;
            }
        }
    
        &__list {
            max-width: 100%;
            margin-bottom: 30px;
            & ol {
                display: flex;
                flex-direction: column;
                gap: 20px;
                list-style-type: none;
                counter-reset: item;
    
                & li {
                    padding: 24px;
                    padding-left: 100px;
    
                    &::before {
                        width: 60px;
                        height: 60px;
                        left: 20px;
                        top: 32px;
                    }
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .banner {
        padding: 80px 0;
        &__bg {
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            width: 100%;
            height: 100%;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: bottom;
            }
        }
    
        &__subtitle {
            font-size: 20px;
            margin-bottom: 20px;
        }
    
        &__text {
            border-radius: 0;
            padding: 0;
            background: transparent;
            margin-bottom: 30px;
        }
    
        &__btns {
            flex-direction: column;
        }
    
        &__list {
            & ol {
                gap: 10px;
    
                & li {
                    border-radius: 10px;
                    padding: 10px;
                    padding-left: 70px;
                    &::before {
                        width: 40px;
                        height: 40px;
                        left: 10px;
                        top: 10px;
                        
                    }
    
                    & p {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}