
/* Общие glob */

html.lock {
    overflow: hidden;
}
html.burger-lock {
    overflow: hidden;
}
html.lock::before {
    opacity: 1;
}
html::before {
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3215686275);
    position: fixed;
    inset: 0;
    opacity: 0;
    z-index: 10;
    pointer-events: none;
    -webkit-transition: opacity 0.3s linear;
    -o-transition: opacity 0.3s linear;
    transition: opacity 0.3s linear;
}
.no-scroll {
    overflow: hidden;
}
body.lock {
    overflow: hidden;
}
body.burger-lock {
    overflow: hidden;
}

body {
    min-height: 100vh;
    margin: 0;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: #424242;
    font-family: var(--font-family);
    background-color:  #fff;
    cursor: url("./../img/cursor.png"), auto;
}
a, button {
    cursor: url("./../img/cursor-pointer.png"), pointer;
}
.container {
	margin: 0 auto;
	padding: 0 var(--container-padding);
	max-width: var(--container-width);
	width: 100%;
}

.section-offset {
    margin-bottom: 120px;
}
.section-padding {
    padding: 120px 0;
}

.section__title {
    text-align: center;
    margin-bottom: 50px;
}

.section__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 50px;
}

.line-bottom {
    padding-bottom: 120px;
    position: relative;
    &::after {
        content: "";
        display: block;
        width: calc(100% - 20px);
        max-width: 1000px;
        height: 1px;
        padding: 0 10px;
        background: #3d5648;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 0);
        
    }
}

/*  titles  */
.title-h1, .title-h2, .title-h3, .title-h4, .title-h5, .title-h6 {
    font-family: var(--second-family);
    font-weight: 700;
    text-transform: uppercase;
    color: #424242;
    line-height: 120%;
}
.title-h1 {
    font-size: 50px;
    line-height: 120%;
}
.title-h2 {
    font-size: 40px;
}
.title-h3 {
    font-size: 35px;
}
.title-h4 {
    font-size: 30px;
}
.subtitle {
    font-weight: 500;
    line-height: 1.36364;
    color: #212121;
    margin-bottom: 60px;
    & p:not(:last-child) {
        margin-bottom: 20px;
    }
}

@media (max-width: 1200px) {
    body {
        font-size: 16px;
    }
    .title-h1 {
        font-size: 40px;
    }
    .title-h2 {
        font-size: 34px;
    }
    .title-h3 {
        font-size: 28px;
    }
    .title-h4 {
        font-size: 24px;
    }
}
@media (max-width: 768px) {
    .section__title {
        margin-bottom: 40px;
    }
    .title-h1 {
        font-size: 30px;
    }
    .title-h2 {
        font-size: 30px;
    }
    .title-h3 {
        font-size: 24px;
    }
    .title-h4 {
        font-size: 21px;
    }
    .line-bottom {
        padding-bottom: 40px;
        position: relative;
        &::after {
            background: #dfe1e0;
        } 
    }
    .section-offset {
        margin-bottom: 80px;
    }
    .section-padding {
        padding: 80px 0;
    }
    .section__wrap {
        display: flex;
        flex-direction: column;
        gap: 40px;
        & .section__top {
            display: contents;
        }
        & .section__btn {
            order: 3;
        }
    }
    .subtitle {
        line-height: 1.5;
        margin-bottom: 40px;
    }
}


/*  buttons */
.primary-btn, .secondary-btn {
    display: block;
    border-radius: 10px;
    padding: 18px 40px;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 16px;
    text-align: center;
    transition: all 0.2s ease-in;
}
.primary-btn {
    background: #997000;
}
.secondary-btn {
    background: #56692e;
}
@media (hover: hover) {
    .primary-btn:hover {
        background: #cc9600;
    }
    .secondary-btn:hover {
        background: #718d3f;
    }
}

@media (max-width: 768px) {
    .primary-btn, .secondary-btn {
        padding: 18px 14px;
    }
}

