        mask: url('data:image/svg+xml;utf8,<svg width="23" height="18" viewBox="0 0 23 18" fill="none" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" clip-rule="evenodd" d="M23 9.00001C23 9.23099 22.9114 9.45257 22.7535 9.61585L14.8998 17.7449C14.5712 18.085 14.0385 18.085 13.7099 17.7449C13.3813 17.4048 13.3813 16.8534 13.7099 16.5132L20.127 9.87098H0.841463C0.37674 9.87098 0 9.48102 0 9.00001C0 8.51901 0.37674 8.12904 0.841463 8.12904H20.127L13.7099 1.48684C13.3813 1.1467 13.3813 0.595229 13.7099 0.255098C14.0385 -0.0850327 14.5712 -0.0850327 14.8998 0.255098L22.7535 8.38418C22.9114 8.54746 23 8.76903 23 9.00001Z" fill="black"/></svg>') no-repeat center;
        mask-size: contain;
        -webkit-mask-size: contain;
        transition: all 0.3s ease;
    }

    &:hover {
