        }
    }
}



.logo {
    display: flex;
    align-items: center;
    gap: 13px;

    &__picture {
        flex-shrink: 0;
        & img {
            display: block;
            width: 58px;
            aspect-ratio: 1 / 1;
            object-fit: contain;
        }
    }

    &__text {
        & p {
            font-family: "Cormorant Garamond", sans-serif;
            font-weight: 700;
            font-size: 32px;
            letter-spacing: 0.03em;
            text-align: center;
            color: #000;
            text-wrap: nowrap;
        }
        & span {
            font-family: "Montserrat", sans-serif;
            font-weight: 400;
            font-size: 13px;
            letter-spacing: -0.05em;
            color: #838383;
            text-wrap: nowrap;
        }
    }
}

.networks {
    display: flex;
    gap: 10px;
}
.network {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    border: 1px solid #997000;
    transition: all 0.3s ease;
    & svg path {
        transition: all 0.3s ease;
    }
    
    &.white {
        border: 1px solid #fff;
        & svg path {
            fill: #fff;
        }
    }
}
@media (hover: hover) {
    .network:hover {
        background: #997000;
        & svg path {
            fill: #fff;
        }
    }
    .network.white:hover {
        background: #fff;
        & svg path {
            fill: #997000;
        }
    }
}

.phone {
    font-weight: 700;
    font-size: 21px;
    letter-spacing: 0.01em;
    color: #56692e;
    text-wrap: nowrap;
    transition: all 0.3s ease;

    &.white {
        color: #fff;
    }

    &:hover {
        color:  #997000;
    }
}

@media (max-width: 1200px) {
    .logo {
        gap: 10px;
    
        &__picture  img {
            width: 50px;
        }
    
        &__text {
            & p {
                font-size: 28px;
            }
            & span {
                font-size: 12px;
            }
        }
    }
    .network {
        width: 40px;
        height: 40px;  
    }
    
    .phone {
        font-size: 18px;
    } 
}
@media (max-width: 1024px) {
    .logo {
        gap: 5px;
        &__picture img {
             width: 40px;
        }
        &__text {
            & p {
                font-size: 20px;
            }
            & span {
                display: none;
            }
        }
    }
    
    .networks {
        gap: 5px;
    }
    .network {
        width: 35px;
        height: 35px;
    }
    
    .phone {
        font-size: 21px;
    }
}




/*  form  */
input, textarea {
    border: 1px solid #dbe1dd;
    border-radius: 10px;
    padding: 18px;
    font-size: 16px;
    color: #000;
    transition: all 0.3s ease;
    &::placeholder {
        font-size: 16px;
        color: #000;
    }
    &:hover {
        border: 1px solid #bfc4c2;
    }
    &:focus, &:active {
        border: 1px solid #996b3e;
    }
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	-webkit-appearance:none;
}

textarea {
    outline: none;
    width: 100%;
    min-height: 112px;
    resize: none;
}

.politic {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 14px;
    line-height: 120%;
    color: #424242;
    & a {
        color: inherit;
        text-decoration: underline;
        &:hover {
            opacity: 0.7;
        }
    }

    & input[type="checkbox"] {
        position: relative;
        width: 30px;
        height: 30px;
        color: #000;
        border: 1px solid #dbe1dd;
        border-radius: 5px;
        appearance: none;
        outline: 0;
        cursor: url("./../img/cursor-pointer.png"), pointer;
        transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
        flex-shrink: 0;
        &::before {
            position: absolute;
            content: '';
            display: block;
            top: 50%;
            left: 50%;
            width: 10px;
            height: 20px;
            border-style: solid;
            border-color: #424242;
            border-width: 0 3px 3px 0;
            transform: translate(-50%, -60%) rotate(45deg);
            opacity: 0;
        }
        &:checked {
            &::before {
                opacity: 1;
            }
        }
    }
}

/* select */
select {
    width: 100%;
    max-width: 100%;
    cursor: url("./../img/cursor-pointer.png"), pointer;
    outline: none;
    border: 1px solid #dbe1dd;
    border-radius: 10px;
    padding: 18px;
    font-size: 16px;
    color: #000;
    padding-right: 30px;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    background: #fff url("./../img/icons/arrow-bottom.svg") no-repeat right;
    background-position-x: calc(100% - 10px);
    transition: all 0.3s ease;

    &:hover {
        border: 1px solid #bfc4c2;
    }
    &:focus, &:active {
        border: 1px solid #996b3e;
    }
}

@media (max-width: 768px) {
    input, textarea {
        font-size: 14px;
        &::placeholder {
            font-size: 14px;
        }
    }
    select {
        font-size: 14px;
    }
    .politic {
        align-items: flex-start;
    
        & input[type="checkbox"] {
            width: 20px;
            height: 20px;
            &::before {
                width: 6px;
                height: 12px;
            }
        }
    }
}

// pagination
.pagination {
    display: flex;
    gap: 10px;
    text-align: center;
    margin-top: 20px;
}

.pagination-btn {
    border: 1px solid #dbe1dd;
    border-radius: 10px;
    padding: 18px 13px;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 58px;
    height: 58px;
}

.pagination button.active {
    border: 1px solid #56692e;
    background: #56692e;
    color: #fff;
}

.prev-button, .next-button {
    font-weight: 600;
    font-size: 18px;
    &:hover {
        color: #56692e;
        &::after {
            background: #56692e;
        }
    }
    &.disabled {
        opacity: 0.7;
        pointer-events: none;
    }
}
.prev-button {
    padding-right: 20px;
    flex-direction: row-reverse;
    &::after {
        transform: rotate(180deg);
    }
}
.next-button {
    padding-left: 20px;
}

.hidden {
    display: none !important;
}

@media (max-width: 768px) {
    .pagination {
        align-items: center;
        justify-content: center;
        gap: 5px;
    }
    
    .pagination-btn {
        font-size: 15px;
        width: 36px;
        height: 36px;
    }

    
    .prev-button span, .next-button span {
        display: none;
    }
    .prev-button {
        padding-right: 15px;
    }
    .next-button {
        padding-left: 15px;
    }
}


/* accordion */
.accordion {
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    cursor: url("./../img/cursor-pointer.png"), pointer;
    width: 100%;
    position: relative;

    &--active {
        .accordion__btn {
            transform: rotate(180deg);
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        -webkit-transition: all 0.4s ease;
        -o-transition: all 0.4s ease;
        transition: all 0.4s ease;
    }
}

/* tabs */
.tab-btn {
    transition: all 0.3s ease;
}
.tab-content {
    display: none;
    &--active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}


/* filter */
.filter-btns {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 40px;
}

.filter-btn {
    border: 1px solid #dbe1dd;
    border-radius: 10px;
    padding: 10px 20px;
    font-weight: 500;
    color: #000;
    min-width: fit-content;
    &.active {
        background: #dbe1dd;
    }
}
@media (max-width: 800px) {
    .filter-btns {
        flex-wrap: nowrap;
        margin-bottom: 30px;
        overflow-x: auto;
        &::-webkit-scrollbar {
            width: 0;
            height: 0;
        }
    }
}


/* breadcrumbs */
.page-top {
    margin-bottom: 40px;
    & h1 {
        max-width: 1200px;
        margin-bottom: 40px;
    }
}

.breadcrumbs{
    padding-top: 30px;
    margin-bottom: 50px;
}
.breadcrumbs, .breadcrumbs li {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    gap: 2px 15px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.breadcrumbs li, .breadcrumbs li a {
    font-weight: 400;
    font-size: 15px;
    line-height: 1.86667;
    color: #212121;
}
.breadcrumbs li a:hover {
    text-decoration: underline;
}
.breadcrumbs li:after {
    content: "";
    display: inline-block;
    width: 6px;
    height: 10px;
    background-image: url('./../img/icons/arrow-right.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}
.breadcrumbs li:last-child::after {
    content: none;
}


/*  media global  */
@media (max-width: 768px) {
    .page-top {
        margin-bottom: 30px;
        & h1 {
            margin-bottom: 30px;
        }
    }
    
    .breadcrumbs {
        padding-top: 20px;
        margin-bottom: 40px;
    }
    .breadcrumbs li, .breadcrumbs li a {
        font-size: 15px;
    }
}


