
// doctors
.doctors {
    overflow: hidden;

    &__bg {
        background: rgba(86, 105, 46, 0.05);
    }

    &__swiper {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 30px 20px;
        overflow: visible;

        & .swiper-wrapper, & .swiper-slide {
            display: contents;
        }
    }

    &__swiper-btns {
        display: none;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 30px 20px;
        padding-bottom: 30px;
        overflow: visible;
    }

    &__card {
        display: flex;
        flex-direction: column;
        border-radius: 20px;
        color: #212121;
        transition: all 0.3s ease;
        background: #fff;
        &:hover {
            box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 0.1);
        }

        &-content {
            flex-grow: 1;
            padding: 30px;
            display: flex;
            flex-direction: column;
        }

        & .link {
            margin-top: auto;
        }
    }

    &__card-picture {
        & img {
            border-radius: 20px;
            width: 100%;
            height: 318px;
            object-fit: cover;
        }
    }

    &__card-fio {
        font-weight: 500;
        font-size: 22px;
        color: #212121;
        padding-bottom: 20px;
        margin-bottom: 20px;
        position: relative;
        &::after {
            content: "";
            display: block;
            width: 80px;
            height: 1px;
            background: #ca9561;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }

    &__card-post {
        font-size: 16px;
        color: #996b3e;
        margin-bottom: 30px;
    }

    &__card-exp {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        margin-bottom: 30px;
    }
}

@media (max-width: 1200px) {
    .doctors {
        &__swiper, &__cards {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}
@media (max-width: 890px) {
    .doctors {
        &__swiper, &__cards {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}
@media (max-width: 768px) {
    .doctors {
        position: relative;

        &__bg {
            background: rgba(86, 105, 46, 0.05);
            & .doctors__swiper-btns {
                top: 80px;
            }
        }

        & .title-h2 {
            max-width: calc(100% - 90px);
            & span {
                display: none;
            }
        }

        &__swiper-btns {
            display: flex;
            position: absolute;
            top: 0;
            right: 10px;
        }

        &__swiper {
            display: block;
            & .swiper-wrapper {
                display: flex;
            }
            & .swiper-slide {
                display: block;
            }
        }
    
        &__card-content {
            padding: 20px;
        }
    
        &__card-picture {
            & img {
                height: 240px;
                object-position: top;
            }
        }
    
        &__card-fio {
            font-size: 18px;
        }
    
        &__card-post {
            font-size: 14px;
            margin-bottom: 20px;
        }
    
        &__card-exp {
            margin-bottom: 20px;
        }
    }
}
@media (max-width: 540px) {
    .doctors {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
        }
    }
}