
// test
.test {
    &__list {
        display: flex;
        flex-direction: column;
        gap: 30px;
        list-style-type: none;
        counter-reset: item;
        margin-bottom: 40px;
    }

    &__item {
        padding-bottom: 30px;
        border-bottom: 1px solid rgba(61, 86, 72, 0.25);
        font-weight: 500;
        color: #212121;
        & p {
            margin-bottom: 30px;
            &::before {
                content: counter(item) ". ";
                counter-increment: item;
            }
        }
    }

    &__result {
        border-radius: 20px;
        padding: 30px;
        background: #fbfbfb;
        margin-top: 60px;
    }
}

@media (max-width: 768px) {
    .test {  
        &__result {
            padding: 20px;
            margin-top: 40px;
        }
    }
}


.checkbox-toggle {
	display: flex;
    align-items: center;
    gap: 20px;    
	position: relative;
	vertical-align: middle;
	user-select: none;	
}
.checkbox-toggle .checkbox-toggle-switch {
	display: inline-block;	
    width: 60px;
    height: 25px;
	border-radius: 50px;
	position: relative;
	vertical-align: top;
	background: #996b3e;
	transition: .2s;
}
.checkbox-toggle .checkbox-toggle-switch:before {
	content: '';
	display: inline-block;	
	width: 28px;	
	height: 28px;
	position: absolute;
    bottom: 0;
	left: -1px;
	background: #efefef;
	border-radius: 50%;
	transition: .15s;		
}
.checkbox-toggle input[type=checkbox] {
	display: block;	
	width: 0;
	height: 0;	
	position: absolute;
	z-index: -1;
	opacity: 0;
}
.checkbox-toggle input[type=checkbox]:checked + .checkbox-toggle-switch {
	background: #56692e;
}
.checkbox-toggle input[type=checkbox]:checked + .checkbox-toggle-switch:before {
	transform:translateX(33px);
}

.checkbox-toggle .checkbox-toggle-text {
    position: relative;
    width: 35px;
    height: 25px;
}
.checkbox-toggle .checkbox-toggle-text:before {
    content: "Нет";
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity 0.2s;
}
.checkbox-toggle .checkbox-toggle-text:after {
    content: "Да";
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity 0.2s;
}
.checkbox-toggle input[type=checkbox]:checked ~ .checkbox-toggle-text:before {
    opacity: 0;
}
.checkbox-toggle input[type=checkbox]:checked ~ .checkbox-toggle-text:after {
    opacity: 1;
}
