
// blockquote
.blockquote {
    &__inner {
        display: flex;
        gap: 60px;
        border: 1px solid #dbe1dd;
        border-radius: 20px;
        padding: 30px;
    }

    &__picture {
        display: block;
        max-width: 360px;
        min-width: 220px;
        width: 100%;
        background: #EFEFEF;
        border-radius: 10px;
        flex: 1 0 15%;
        & img {
            border-radius: 10px;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__content {
        flex: 1 0 60%;
    }

    &__icon {
        display: block;
        margin-bottom: 40px;
    }

    &__text {
        margin-bottom: 40px;
    }

    &__fio {
        font-weight: 500;
        font-size: 24px;
        color: #212121;
        margin-bottom: 10px;
    }

    &__post {
        color: #996b3e;
    }
}

@media (max-width: 768px) {
    .blockquote {
        &__inner {
            flex-direction: column;
            gap: 30px;
            padding: 20px;
        }
    
        &__picture {
            margin: 0 auto;
        }
        &__icon {
            margin-bottom: 20px;
        }
        &__text {
            margin-bottom: 20px;
        }
        &__fio {
            font-size: 20px;
        }
    }
}
@media (max-width: 640px) {
    .blockquote {
        &__picture {
            max-width: 100%;
            min-width: 100%;
            & img {
                max-height: 300px;
                min-height: 230px;
                object-position: top;
            }
        }
    }
}
