
// text-block
.text-block, .text-block .container {
    & h2, & h3, & h4, & h5, & h6 {
        font-family: var(--second-family);
        font-weight: 700;
        text-transform: uppercase;
        color: #424242;
        line-height: 120%;
        margin-bottom: 30px;
        &:not(:first-child) {
            margin-top: 50px;
        }
    }
    & h2 {
        font-size: 40px;
    }
    & h3 {
        font-size: 35px;
    }
    & h4 {
        font-size: 30px;
    }
    & h5 {
        font-size: 25px;
    }
    & h2:empty, & h3:empty, & h4:empty, & h5:empty, & h6:empty {
        margin: 0;
    }

    & > p {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    & a {
        text-decoration: underline;
        color: #996b3e;
        transition: all 0.3s ease;
        &:hover {
            color: #56692e;
        }
    }

    & ul {
        display: flex;
        flex-direction: column;
        gap: 5px;
        margin: 20px 0;
        & li {
            position: relative;
            padding-left: 25px;
            &::before {
                content: "";
                display: block;
                width: 5px;
                height: 5px;
                border-radius: 100%;
                background-color: #996b3e;
                position: absolute;
                top: 11px;
                left: 10px;
            }
        }
    }

    & ol {
        display: flex;
        flex-direction: column;
        gap: 5px;
        margin: 20px 0;
        list-style-type: none;
        counter-reset: item;
        & li {
            position: relative;
            padding-left: 34px;
            &::before {
                content: counter(item) ". ";
                counter-increment: item;
                display: block;
                position: absolute;
                top: 0;
                left: 10px;
            }
        }
    }

    & > picture {
        display: block;
        width: 100%;
        max-width: 50%;
        margin: 20px 0;
        
        &.left {
            float: left;
            margin-right: 40px;
        }
        &.right {
            float: right;
            margin-left: 40px;
        }

        & img {
            border-radius: 20px;
            width: 100%;
            height: 100%;
            max-height: 480px;
            min-height: 200px;
            object-fit: cover;
        }
    }

    & table {
        display: block;
        width: fit-content;
        max-width: 100%;
        border-radius: 20px;
        overflow: hidden;
        border-collapse: collapse; 
        margin: 20px 0;
        border: 1px solid #dbe1dd;

        & thead {
            & th {
                border: none;
                padding: 20px 25px;
                font-weight: 500;
                font-size: 14px;
                border-right: 1px solid #dbe1dd;
                border-bottom: 1px solid #dbe1dd;
            }
            & tr th:last-child {
                border-right: none;
            }
            & & tr:last-child th {
                border-bottom: none;
            }
        }

        & tbody {
            & td {
                border-right: 1px solid #dbe1dd;
                border-bottom: 1px solid #dbe1dd;
                padding: 20px;
                font-size: 14px;
                vertical-align: top;
            }
            & tr td:last-child {
                border-right: none;
            }
            & & tr:last-child td {
                border-bottom: none;
            }
        }
    }
}

.literature {
    border-radius: 10px;
    padding: 30px;
    background: #fbfbfb;
    margin: 40px 0 20px;

    &__title {
        font-weight: 500;
        color: #212121;
    }
}

.author {
    border-radius: 10px;
    padding: 30px;
    background: #fbfbfb;
    display: flex;
    align-items: center;
    gap: 30px;
    color: #212121;

    &__photo {
        display: block;
        width: 100%;
        max-width: 150px;
        aspect-ratio: 1 / 1;
        & img {
            border-radius: 10px;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__name {
        margin-bottom: 10px;
        & span {
            font-weight: 600;
        }
    }

    &__post {
        font-size: 16px;
        color: #996b3e;
        position: relative;
        padding-bottom: 20px;
        margin-bottom: 20px;
        &::after {
            content: "";
            display: block;
            width: 80px;
            height: 1px;
            background: #ca9561;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }
}


// media text-block
@media (max-width: 1200px) {
    .text-block {
        & h2 {
            font-size: 24px;
        }
        & h3 {
            font-size: 28px;
        }
        & h4 {
            font-size: 24px;
        }
        & h5 {
            font-size: 20px;
        }
    }
}
@media (max-width: 768px) {
    .text-block, .text-block .container  {
        & h2, & h3, & h4, & h5, & h6 {
            margin-bottom: 20px;
            &:not(:first-child) {
                margin-top: 40px;
            }
        }
        & h2 {
            font-size: 27px;
        }
        & h3 {
            font-size: 24px;
        }
        & h4 {
            font-size: 21px;
        }
        & h5 {
            font-size: 18px;
        }
        & > picture {
            max-width: 100%;
            margin: 20px 0 !important;
            float: none !important;
            & img {
                max-height: 220px;
            }
        }
        & table {
            overflow-x: auto;
            max-width: 100%;
            & thead th, & tbody td {
                min-width: 243px;
            }
            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }
    }

    .literature {
        padding: 20px;
        margin: 20px 0;
    }
}

@media (max-width: 500px) {
    .author {
        padding: 20px;
        flex-direction: column;
        align-items: flex-start;
    }
}