
// doctor
.doctor {

    &__wrap {
        display: flex;
        gap: 60px;
    }

    &__left {
        flex: 1 0 62%;
    }

    &__name {
        margin-bottom: 40px;
    }

    &__info {
        border: 1px solid rgba(86, 105, 46, 0.3);
        border-radius: 20px;
        padding: 30px;
        margin-bottom: 30px;
        display: flex;
        gap: 60px;

        &-item {
            display: flex;
            align-items: center;
            gap: 20px;
        }

        &-icon {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 60px;
            height: 60px;
            border-radius: 10px;
            background: rgba(61, 86, 72, 0.05);
            flex-shrink: 0;
        }

        &-text {
            & span {
                display: block;
                margin-bottom: 5px;
            }
            & p {
                font-weight: 600;
                color: #424242;
            }
        }
    }

    &__block {
        margin-bottom: 40px;
        &-title {
            font-family: "Cormorant Garamond", sans-serif;
            font-weight: 700;
            font-size: 24px;
            text-transform: uppercase;
            color: #424242;
        }
    }

    &__btns {
        display: flex;
        gap: 20px;
        margin-bottom: 40px;
    }

    &__right {
        flex: 1 0 30%;
    }

    &__photo {
        display: block;
        width: 100%;
        margin-bottom: 40px;
        & img {
            width: 100%;
            height: 100%;
            min-height: 450px;
            max-height: 500px;
            object-fit: cover;
            object-position: top;
            border-radius: 20px;
        }
    }

    &__shedule {
        border-radius: 20px;
        padding: 30px;
        background: #fbfbfb;
        &-title {
            font-weight: 700;
            font-size: 20px;
            line-height: 1.3;
            color: #424242;
            margin-bottom: 30px;
        }

        & ul {
            & li {
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 12px;
                font-size: 16px;
                line-height: 1.3;
                color: #424242;
                &:not(:last-child) {
                    padding-bottom: 12px;
                    border-bottom: 1px solid rgba(61, 86, 72, 0.1);
                    margin-bottom: 12px;
                }
                & span:last-child {
                    font-weight: 600;
                    font-size: 16px;
                }
            }
        }
    }
}

@media (max-width: 1200px) {
    .doctor {

        &__wrap {
            flex-direction: column;
            gap: 0px;
        }
    
        &__left, &__right {
            display: contents;
        }
    
        &__info {
            order: 2;
            justify-content: space-between;
        }
    
        &__block {
            order: 3;
            margin-bottom: 40px;
            &:last-child {
                order: 6;
            }
        }
    
        &__btns {
            order: 4;
        }
    
        &__photo {
            order: 1;
            max-width: 600px;
            margin: 0 auto;
            margin-bottom: 40px;
        }
    
        &__shedule {
            order: 5;
            margin-bottom: 40px;
        }
    }
}
@media (max-width: 768px) {
    .doctor {
        &__name {
            margin-bottom: 30px;
        }
    
        &__info {
            padding: 20px;
            flex-direction: column;
            gap: 20px;
            &-icon {
                width: 40px;
                height: 40px;
                padding: 10px;
            }
            &-text p, &-text span {
                font-size: 15px;
            }
        }
    
        &__block {
            &-title {
                font-size: 20px;
            }
        }
    
        &__btns {
            flex-direction: column;
        }
    
        &__photo {
            max-width: 500px;
            margin-bottom: 20px;
            & img {
                min-height: 310px;
                max-height: 400px;
            }
        }
    
        &__shedule {
            padding: 20px;
            &-title {
                font-size: 16px;
                margin-bottom: 20px;
            }
    
            & ul {
                & li {
                    font-size: 14px;
                    & span:last-child {
                        font-weight: 600;
                        font-size: 14px;
                    }
                }
            }
        }
    }
}
@media (max-width: 600px) {
    .doctor {
        &__photo {
            max-width: 100%;
            margin-bottom: 20px;
            & img {
                min-height: 310px;
                max-height: 360px;
            }
        }
    }
}