
// steps
.steps {

    &__swiper {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
        list-style-type: none;
        counter-reset: item;

        & .swiper-wrapper, & .swiper-slide {
            display: contents;
        }
    }

    & .mobile {
        display: none;
    }

    &__card {
        border: 1px solid #3d5648;
        border-radius: 10px;
        padding: 30px;

        &-name {
            padding: 0;
            border: none;
            display: flex;
            align-items: center;
        }
    }

    &__card-img {
        display: block;
        margin-bottom: 30px;
        & img {
            border-bottom: 5px solid #997000;
            border-radius: 15px;
            width: 100px;
            height: 100px;
            object-fit: cover;
        }
    }

    &__card-title {
        font-family: "Cormorant Garamond", sans-serif;
        font-weight: 700;
        font-size: 24px;
        text-transform: uppercase;
        color: #424242;
        margin-bottom: 20px;

        &::before {
            content: counter(item) ". ";
            counter-increment: item;
            font-size: 32px;
        }
    }

    &__card-text {
        max-width: 360px;
    }
}

@media (max-width: 1100px) {
    .steps {
        &__swiper {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

@media (max-width: 768px) {
    .steps {
        overflow: hidden;

        &__swiper {
            display: block;
            overflow: visible;
    
            & .swiper-wrapper {
                display: flex;
            }
            & .swiper-slide {
                display: block;
                &:first-child {
                    display: none;
                }
            }
        }
    
        & .mobile {
            display: block;
        }
    
        &__swiper-slide{
            height: auto;
        }

        &__card {
            padding: 20px;
            height: 100%;
        }
    
        &__card-img {
            margin-bottom: 20px;
            & img {
                border-radius: 10px;
            }
        }
    
        &__card-title {
            font-size: 20px;
            margin-bottom: 10px;
    
            &::before {
                font-size: 28px;
            }
        }
    }
}