
// services
.services {
    &__title {
        margin-bottom: 30px;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 60px 20px;
    }

    &__card {
        border-radius: 20px;
        background: #fbfbfb;
    }

    &__card-picture {
        display: block;
        width: 100%;
        & img {
            border-radius: 20px;
            width: 100%;
            height: 300px;
            object-fit: cover;
        }
    }

    &__card-content {
        padding: 30px;
    }

    &__card-title {
        font-weight: 700;
        font-size: 20px;
        color: #996b3e;
        padding-bottom: 20px;
        margin-bottom: 20px;
        position: relative;
        &::after {
            content: "";
            display: block;
            width: 80px;
            height: 1px;
            background: #ca9561;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }

    &__card-item {
        padding: 20px 0;
        &:not(:last-child) {
            border-bottom: 1px solid rgba(86, 105, 46, 0.3);
        }
        & a {
            font-weight: 500;
            line-height: 1.33333;
            color: #424242;
            transition: all 0.3s ease;
            &:hover {
                color: #996b3e;
            }
        }
        & a.link {
            padding-top: 30px;
            font-weight: 700;
        }
    }

    &__card-acc {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
    }

    &__card-sublist {
        padding-top: 15px;
        padding-left: 40px;
        & li {
            list-style: disc;
        }
        & a {
            display: block;
            padding: 5px 0;
            font-size: 16px;
        }
    }

    &__btn {
        width: fit-content;
        margin: 50px auto 0;
    }
}

@media (max-width: 1160px) {
    .services {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 60px 20px;
        }
        &__card-picture {
            & img {
                height: 260px;
            }
        }
    }    
}
@media (max-width: 768px) {
    .services {
        &__title {
            margin-bottom: 20px;
        }

        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }

        &__card-picture {
            & img {
                height: 200px;
            }
        }
    
        &__card-content {
            padding: 30px 20px 20px;
        }
    
        &__card-item {
            padding: 15px 0;
        }
    
        &__card-sublist {
            padding-top: 15px;
            padding-left: 20px;
            & a {
                font-size: 14px;
            }
        }

        &__btn {
            width: 100%;
            margin-top: 20px;
        }
    }
}