.header {
    border-bottom: 1px solid #dfe1e0;
    background-color: #fff;
    position: relative;
    
    &__top {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 100;
        background-color: #fff;
    }

    &__top-row {
        padding: 26px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 20px;
        border-bottom: 1px solid #dfe1e0;
    }

    &__top-item {
        display: flex;
        align-items: center;
        gap: 40px;
    }

    &__adres {
        display: flex;
        align-items: center;
        gap: 10px;
        font-weight: 400;
        font-size: 14px;
        line-height: 1.14286;
        color: #424242;
        & span {
            display: block;
        }
    }

    &__links {
        & a {
            font-family: "Gilroy", sans-serif;
            font-weight: 500;
            font-size: 20px;
            color: #424242;
        }
    }

    &__bottom {
        margin-top: 110px;
    }

    &__nav {
        &-list {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 16px;
        }

        &-item {
            position: relative;
            padding-right: 30px;
            &:hover {
                .header__nav-submenu {
                    opacity: 1;
                    visibility: visible;
                    pointer-events: all;
                }
            }

            &:first-child {
                .header__nav-submenu {
                    left: 0;
                }
            }
            &:nth-last-child(-n+4) {
                .header__nav-submenu {
                    left: auto;
                    right: 0;
                }
            }
        }

        &-arrow {
            position: absolute;
            top: 26px;
            right: 0;
            transition: all 0.3s ease;
        }

        &-link {
            display: block;
            font-weight: 400;
            font-size: 20px;
            color: #424242;
            padding: 26px 0;
        }

        &-submenu {
            border-radius: 0 0 20px 20px;
            padding: 20px 0;
            background: #fff;
            border: 1px solid #4242422c;
            position: absolute;
            top: 100%;
            left: -30px;
            min-width: 320px;
            opacity: 0;
            z-index: 3;
            visibility: hidden;
            pointer-events: none;
            transition: all 0.3s ease;

            & li {
                //position: relative;
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 10px;
                padding: 10px 30px;

                & .header__nav-arrow {
                    position: static;
                    top: 10px;
                    right: 30px;
                }

                &:hover {
                    & > .header__nav-subsubmenu {
                        opacity: 1;
                        visibility: visible;
                        pointer-events: all;
                    }
                    & > a {
                        color: #996b3e;
                    }
                }
            }

            & a {
                display: block;
                font-weight: 400;
                font-size: 18px;
                color: #424242;
                transition: all 0.3s ease;
                &:hover {
                    color: #996b3e;
                }
            }
        }

        &-subsubmenu {
            border-radius: 0 0 20px 20px;
            padding: 20px 0;
            background: #fff;
            border: 1px solid #4242422c;
            position: absolute;
            top: 0;
            left: 100%;
            min-width: 320px;
            opacity: 0;
            z-index: 3;
            visibility: hidden;
            pointer-events: none;
            transition: all 0.3s ease;
        }
    }

    &__burger {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-end;
        width: 23px;
        height: 18px;

        & span {
            display: block;
            width: 100%;
            height: 2px;
            background: #000;
            transition: all 0.3s ease;
            &:nth-child(2) {
                width: 12px;
            }
            &:nth-child(3) {
                width: 17px;
            }
        }

        &.open {
            & span {
                &:nth-child(1) {
                    transform: rotate(45deg);
                    transform-origin: top left;
                }
                &:nth-child(2) {
                    opacity: 0;
                }
                &:nth-child(3) {
                    width: 100%;
                    transform: rotate(-45deg);
                    transform-origin: bottom left;
                }
            }
        }
    }
}

.header-mobile {
    &__wrapper {
        display: none;
    }
    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        padding: 10px 0;
    }
}

@media (hover :hover) {
    .header {
        &__nav {
            &-item:hover {
                & > .header__nav-arrow {
                    transform: rotate(180deg);
                }
            }
        }
    }
}

@media (max-width: 1500px) {
    .header {
        &__top-row {
            padding: 26px 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;
            border-bottom: 1px solid #dfe1e0;
        }
    
        &__top-item {
            gap: 20px;
        }
    }
}
@media (max-width: 1360px) {
    .header {
        &__top-item {
            gap: 15px;
        }
        &__links {
            display: none;
        }
        &__btn {
            padding: 18px 20px;
        }
    }
}
@media (max-width: 1260px) {
    .header {
        &__nav {
            &-link {
                font-size: 17px;
            }
    
            &-submenu {
                & li {
                    padding: 10px 20px;
                }
                & a {
                    font-size: 16px;
                }
            }
    
            &-submenu, &-subsubmenu {
                padding: 10px 0;
                min-width: 260px;
            }
        }
    }
}

@media (max-width: 1024px) {
    .header-mobile__wrapper {
        display: block;
    }
    main {
        margin-top: 116px;
    }
    .header {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 100;
        &__top {
            position: static;
        }
        &__top-row {
            padding: 10px 0;
        }
        &__adres, &__contacts {
            display: none;
        }

        &__bottom {
            position: absolute;
            top: 60.5px;
            left: 0;
            right: 0;
            z-index: 10;
            width: 100%;
            height: calc(100vh - 60px);
            background: #fff;
            opacity: 0;
            visibility: hidden;
            pointer-events: none;
            transform: translate(-50%, 0%);
            transition: all 0.3s ease;
            overflow-y: auto;
            margin-top: 0;
            padding: 24px 0;
            &::-webkit-scrollbar {
                width: 4px;
            }
            &::-webkit-scrollbar-track {
                background: #c1c5c3;
            }
            &::-webkit-scrollbar-thumb {
                background: #3d5648;
            }

            &.open {
                opacity: 1;
                visibility: visible;
                pointer-events: all;
                transform: translate(0, 0);
            }

            & .header__links {
                display: flex;
                flex-direction: column;
                gap: 10px;
                margin-bottom: 20px;
                & a {
                    font-weight: 600;
                    font-size: 16px;
                }
            }
        }
    
    
        &__nav {
            padding: 10px 0;
            border-top: 1px solid #dfe1e0;
            border-bottom: 1px solid #dfe1e0;

            &-list {
                flex-direction: column;
                align-items: stretch;
                gap: 0;
            }
    
            &-item {
                position: relative;
                padding-right: 0;
                &.active {
                    & > .header__nav-arrow {
                        transform: rotate(180deg);
                    }
                    & .header__nav-link {
                        color: #996b3e;
                    }
                }
            }
    
            &-arrow {
                position: absolute;
                top: 9px;
                right: 0;
                transition: all 0.3s ease;
                width: 36px;
                text-align: center;
            }
    
            &-link {
                display: block;
                font-size: 16px;
                padding: 10px 0;
                width: fit-content;
            }
    
            &-submenu, &-subsubmenu {
                position: static;
                border-radius: 0;
                padding: 0 10px;
                border: none;
                min-width: none;
                min-width: 100%;

                opacity: 1;
                visibility: visible;
                pointer-events: all;

                max-height: 0;
                overflow: hidden;
                transition: all 0.3s ease;
            }

            &-submenu {
                & li {
                    display: block;
                    padding: 5px 0;
                    position: relative;
    
                    & .header__nav-arrow {
                        position: absolute;
                        top: 6px;
                        right: 0;
                        & svg {
                            transform: rotate(90deg);
                            transition: all 0.3s ease;
                        }
                        &.active svg {
                            transform: rotate(270deg);
                        }
                    }
    
                    &:hover {
                        & > .header__nav-subsubmenu {
                            opacity: 1;
                            visibility: visible;
                            pointer-events: all;
                        }
                        & > a {
                            color: #996b3e;
                        }
                    }
                }
    
                & a {
                    display: block;
                    font-size: 15px;
                }
            }
        }

        &__btn {
            width: 100%;
        }

        &__search {
            padding: 20px 0;
            border-bottom: 1px solid #dfe1e0;
            margin-bottom: 20px;

            &-form {
                position: relative;
            }

            &-input {
                border: 1px solid #424242;
                border-radius: 10px;
                padding: 13px 20px;
                padding-left: 50px;
                width: 100%;
            }

            &-icon {
                position: absolute;
                top: 50%;
                left: 18px;
                transform: translate(0%, -40%);
                
            }
        }
    }

}
